document.addEventListener('DOMContentLoaded', function(){
    App();
});

function App(){
    galeria();
    scrollNav();
    navegacionFija();
}

function navegacionFija(){
    const contHeader = document.querySelector('.header');
    const scrollFestival = document.querySelector('.festival-musica');
    const body = document.querySelector('body');
    window.addEventListener('scroll', function(){
        
        if(scrollFestival.getBoundingClientRect().bottom < 0){
            contHeader.classList.add('header-fijo');
            body.classList.add('espacio-nav');
        }

        else{
            contHeader.classList.remove('header-fijo');
            body.classList.remove('espacio-nav');
        }
    
    });

}
function scrollNav(){
    const enlaces = document.querySelectorAll('.navegacion_principal a');
    
    enlaces.forEach(enlace =>{
        enlace.addEventListener('click', function(evento){
            evento.preventDefault();
            const seccion = evento.target.attributes.href.value;
            const seccionNav = document.querySelector(seccion);
            seccionNav.scrollIntoView({behavior: 'smooth' });
        });
    });
}

function galeria(){
    const imgGaleria = document.querySelector('.galeria-imagenes');

    for(let i = 1; i <= 12; i++){
        const imagen = document.createElement('picture');
        imagen.innerHTML = `
        <source srcset="build/img/thumb/${i}.avif" type="image/avif">
        <source srcset="build/img/thumb/${i}.webp" type="image/webp">
        <img class="imagen-festival border" loading="lazy" src="build/img/thumb/${i}.jpg" alt="Imagen Vocalisata">
        `;
        imgGaleria.appendChild(imagen);
        imagen.onclick = function(){
            mostrarImg(i);
        }
    }

    function mostrarImg(img){
        const imagen = document.createElement('picture');
        imagen.innerHTML = `
        <source srcset="build/img/grande/${img}.avif" type="image/avif">
        <source srcset="build/img/grande/${img}.webp" type="image/webp">
        <img class="imagen-festival border" loading="lazy" src="build/img/grande/${img}.jpg" alt="Imagen Vocalisata">
        `;
        // contenedor de imagen
        const overlayg = document.createElement('DIV');
        overlayg.classList.add('overlayg')
        overlayg.onclick = function(){
            overlayg.remove();
            const body = document.querySelector('body');
            body.classList.remove('fijar-body');
        }        
        overlayg.appendChild(imagen);
        
        //btnCerrar
        const btnCerrar = document.createElement('P');
        btnCerrar.classList.add('btncerrar');
        btnCerrar.textContent = 'X';
        overlayg.appendChild(btnCerrar);
        btnCerrar.onclick = function(){
            overlayg.remove();
            const body = document.querySelector('body');
            body.classList.remove('fijar-body');
        }
        
        // colocando imagen en el body
        const body = document.querySelector('body');
        body.classList.add('fijar-body');
        body.appendChild(overlayg);
    
    }
}